export default {
	data() {
		return {
			nums: [200, 500, 800, 1000],
			price: '',
			defaultImg:'/static/img/header-img.png',
			indexs:0,
			accountData:''
		};
	},
	computed: {
		userInfo() {
			let userInfo = {};
			try {
				userInfo = uni.getStorageSync('userInfo');
				return userInfo;
			} catch (e) {
				// error
			}
			return userInfo;
		}
	},
	onShow(){
		this.getAccountInfo();
	},
	methods: {
		//验证输入框只能输入两位小数
		validatyNum(evt){
			this.price = evt.target.value = (evt.target.value.match(/^\d*(\.?\d{0,2})/g)[0]) || null;
		},
		getNav(url) {
			this.$store.commit('setAccountData',this.accountData);
			this.$Router.push({ name: url});
		},
		//选择金额
		changeNum(num,index) {
			this.indexs = index;
			this.price = num;
		},
		//获取用户信息
		getAccountInfo(){
		},
		//充值
		payment() {
			if (!this.price) {
				uni.showToast({
					icon: 'none',
					title: '请先填写您要充值的金额'
				});
				return;
			}
			let data={
				money:this.price
			}
		}
	}
};
